body {
  background: none;
}

.btn-color-swstcher {
  width: 100px;
  height: 40px;
  color: #000;
  cursor: pointer;
  background: none;
  margin: 20px;
  padding: 10px 25px;
  font-family: Lato, sans-serif;
  font-size: 16px;
  font-weight: 500;
  display: inline-block;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), inset -7px -7px 10px rgba(0, 0, 0, .1), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.btn-4 {
  color: #0dbc27;
  z-index: 1;
  border: 1px solid #0dbc27;
  box-shadow: 0 0 5px #0dbc27, inset 0 0 5px #0dbc27;
}

.btn-4:after {
  content: "";
  width: 0;
  height: 100%;
  z-index: -1;
  background: #0dbc27;
  transition: all .3s;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: 0 0 20px #0dbc27;
}

.btn-4:hover {
  color: #000;
}

.btn-4:hover:after {
  width: 100%;
  left: 0;
}

.btn-4:active {
  top: 2px;
}

.list-btn {
  justify-content: center;
  display: flex;
}

